import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'

Vue.config.productionTip = false

axios.defaults.withCredentials=true;
//axioscookie
Vue.prototype.axios=axios;

//qs,axios post
import qs from 'qs';
Vue.prototype.qs=qs;

//
Vue.prototype.cutIntro=function(arr,num){
  for(var elem of arr){
    if(elem.intro.length>=num){
        var str=elem.intro.substr(0,num);
        for(var i=0;i<str.length;i++){           
          var s=str.substr(str.length-1);
          var reg=/[\w\u4e00-\u9fa5]/;
          if(reg.test(s))
              break;
          else
              str=str.substr(0,str.length-2);
      }
      elem.intro=str+'';       
    }
  }
}

Vue.prototype.cutTit=function(arr,num){
  for(var elem of arr){
    if(elem.title.length>num){
        elem.title=elem.title.substr(0,num)+'';       
    }
  }
}

//
Vue.prototype.spLabel=function(arr,i){
  for(var elem of arr){
    if(elem.label.length>0){
        var labs=elem.label.split('@');
        elem.label=labs[i];       
    }else{
      elem.label='';
    }
  }
}

//
Vue.prototype.loadBackTop=function(){
  document.documentElement.scrollTop=0;
}

//
//2018-12-12 15:12:00
Vue.filter('datetimeFilter',function(val){
  //val 
  //
  var date=new Date(val);
  //
  var y=date.getFullYear();
  var m=date.getMonth()+1;
  var d=date.getDate();
  var h=date.getHours();
  var mi=date.getMinutes();
  var s=date.getSeconds();
  //
  m<10&&(m='0'+m);
  d<10&&(d='0'+d);
  h<10&&(h='0'+h);
  mi<10&&(mi='0'+mi);
  s<10&&(s='0'+s);
  return `${y}-${m}-${d} ${h}:${mi}:${s}`;
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
